import axios from "axios";

class Service {
    url = "http://localhost:8080/";

    async sendGet(url, token, callback) {
        console.log("sendGet", url, typeof token, token)
        // console.log("***** Service.sendGet", `${this.url}${url}?uid=${this.UniqueId}`, key, save);
        const client = new XMLHttpRequest();

        client.addEventListener("readystatechange", () => {
            if (client.readyState === 4 && client.status === 200) {
                console.log(client.responseText);
                callback(JSON.parse(client.responseText));
            }
        });

        client.open("GET", `${this.url}${url}`);
        client.send(JSON.stringify({
            token: token
        }));
        /*
        return await fetch(`${this.url}${url}`, {
            method: 'GET',
            headers: {
                // 'Accept': 'application/json, text/plain',
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                token: token
            })
        });
        /*
        const result = await axios.get(
            `${this.url}${url}`,
            {
                data: {
                    token: token
                },
                headers: {
                    'Content-Type': 'application/json',
                }
            }
        );
        /*
        const result = await axios.get(
            `${this.url}${url}`,
            JSON.stringify(this.getData(token)),
            {
                headers: {
                    'Content-Type': 'application/json',
                }
            }
        );
        const data = result.data;
        console.log(data);

        return data;
        */
    }

    async sendPost(url, sendData, token = null) {
        // console.log("***** Service.sendPost", `${this.url}${url}?uid=${this.UniqueId}`, data);
        const result = await axios.post(
            `${this.url}${url}`,
            JSON.stringify(this.getData(token, sendData)),
            {
                headers: {
                    'Content-Type': 'application/json',
                }
            }
        );
        const data = result.data;
        console.log(data);

        return data;
    }

    getData(token, sendData) {
        if (typeof sendData === "undefined") {
            sendData = {}
        }
        if (typeof token !== "undefined" && token !== null) {
            sendData.token = token;
        }
        return sendData;
    }
}

export default new Service();