<template>
  <div class="login-container">
    <form class="login-form card" v-on:submit.prevent="submit">
      <div class="card-header">INGRESO AL SISTEMA</div>

      <div class="card-body">
        <b-form-group label="EMAIL">
          <b-form-input v-model="form.correo" type="text" />
        </b-form-group>

        <b-form-group label="PASSWORD">
          <b-form-input v-model="form.password" type="password" />
        </b-form-group>
      </div>

      <div
        class="card-footer d-flex justify-content-center mt-2 pt-2 border-top"
      >
        <button
          type="submit"
          class="btn btn-success"
          @click="send"
          size="sm"
        >
          INGRESAR
        </button>
      </div>
    </form>
  </div>
</template>

<script>
import service from "../service/Service.js";

export default {
  name: "LoginComponent",

  data() {
    return {
      form: {
        correo: "david",
        password: 123,
        cliente: "Insomia",
      },
    };
  },

  methods: {
    submit() {
      this.sending = false;
    },

    async send() {
      try {
        var loginResult = await service.sendPost("sesion/login", this.form);
        if (
          loginResult.message == "ok" &&
          loginResult.response !== "undefined" &&
          loginResult.response !== null &&
          loginResult.response != 0
        ) {
          this.$store.dispatch("setToken", loginResult.response);
        } //
        else {
          alert("Verifique usuario y/o password");
        }
        // console.log(typeof loginResult, loginResult);
      } catch (error) {
        //
        alert(error);
      }
    },
  },
};
</script>

<style scoped>
.login-container {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
.login-form {
  width: 90vw;
  max-width: 300px;
}
</style>